efault {\n  components: { pagination },\n  data () {\n    return {\n      token: window.sessionStorage.getItem('bbytoken'),\n      page: 1,//当前页\n      pageCount: 1,\n      pageSmCount: 1,\n      SmPage: 1,\n      tableData: [],\n      addressVisible: false, //地址详情弹框状态\n      address: '', //详情地址,\n      ShopVisible: false, //店铺\n      shop_details: '',\n      ProfitVisible: false, //收益弹框状态\n      ProfitList: [],\n      sellVisible: false, //所售油品 \n      SellOut_Oils: [], //售出的油品详情\n      ReasonData: '',\n      ReasonVisible: false, //关停\n      cancelVisible: false, //取消合作\n      cancelData: '',\n      reason: '', //关停理由\n      oil_shop_id: ''\n    }\n  },\n\n  methods: {\n    async init () {\n      try {\n        let res = await this.$axios.post('admin/OilList/oilList', { token: this.token, page: this.page })\n        if (res.data.code == 1) {\n          this.tableData = res.data.data.list\n          this.pageCount = res.data.data.rows;\n        } else {\n          this.tableData = []\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    /* 收益详情数据 */\n    async Profit (row) {\n      try {\n        let res = await this.$axios.post('admin/OilExamine/incomeRecord', {\n          token: this.token,\n          oil_shop_id: row.id,\n          page: this.SmPage\n        })\n        if (res.data.code == 1) {\n          this.ProfitList = res.data.data.list\n          this.pageSmCount = res.data.data.rows\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    /* 图片放大加旋转 */\n    Shop () {\n      this.$nextTick(() => {\n        let ViewerDome = this.$refs.enlarge\n        if (ViewerDome && ViewerDome.length > 0) {\n          ViewerDome.forEach(item => {\n            Viewer(item)\n          })\n        }\n      })\n    },\n    /* 售出的油品详情 */\n    async Oils (row) {\n      try {\n        let res = await this.$axios.post('admin/OilExamine/oilType', {\n          token: this.token,\n          id: row.id\n        })\n        if (res.data.code == 1) {\n          this.SellOut_Oils = res.data.data\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    /* 关停油站 */\n    ShutDown (row) {\n      if (!this.reason) {\n        this.$message.error('关停理由不能为空')\n        return;\n      }\n      this.operation('admin/OilList/shutDown', this.ReasonData.id)\n    },\n    /* 取消合作 */\n    cancel (row) {\n      if (!this.reason) {\n        this.$message.error('取消合作理由不能为空')\n        return;\n      }\n      this.operation('admin/OilList/totalCancel', this.cancelData.id)\n    },\n    /* 取消/关停 */\n    async operation (url, id) {\n      try {\n        let res = await this.$axios.post(url, {\n          token: this.token,\n          reason: this.reason,\n          oil_shop_id: id\n        })\n        if (res.data.code == 1) {\n          this.$message({ message: res.data.msg, type: 'success' })\n          this.cancelVisible = false\n          this.ReasonVisible = false\n          this.init()\n        } else {\n          this.$message.error(res.data.msg)\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    /* 关闭对话框清空数据 */\n    close () {\n      this.reason = ''\n      this.SmPage = 1\n      this.pageSmCount = 1\n    },\n    currentPage (e) {\n      this.page = e;\n      this.init()\n    },\n    currentSmPage (e) {\n      this.SmPage = e;\n      this.Profit(this.oil_shop_id)\n    }\n  },\n  mounted () {\n    this.init()\n\n  }\n}\n</script>\n\n<style scoped>\n>>> .el-table th {\n  height: 30px !important;\n  line-height: 30px !important;\n}\n.address {\n  width: 100%;\n  min-height: 150px;\n  margin-top: 20px;\n  overflow: auto;\n}\n.shop {\n  min-height: 200px;\n}\n.shop > div {\n  margin-bottom: 10px;\n}\n.shop_image {\n}\n.shop_image > span:first-child {\n}\n.shop_image img {\n  width: 60px;\n  height: 60px;\n  vertical-align: top;\n  margin-right: 10px;\n  border: 1px solid #eee;\n}\n.sell {\n  min-height: 200px;\n}\n</style>\n<style>\n.viewer-container {\n  z-index: 9999 !important;\